//FUNCTIONS
/// Remove the unit of a length
/// @param {Number} $number - Number to remove unit from
/// @return {Number} - Unitless number
@function strip-units($number) {
  @if type-of($number) == 'number' and not unitless($number) {
    @return $number / ($number * 0 + 1);
  }
  @return $number;
}

//MIXINS
// Rem output with px fallback
@mixin font-size($size-value: 1) {
	font-size: ($size-value * strip-units($base-font-size)) * 1px;
	font-size: $size-value * 1rem;
}

@mixin container {
  @include margin(null auto);
  @include padding(null $small-spacing);

  @media #{$tablet-up} {
    max-width: $medium-screen;
  }
}

//TEMP

$spacing: 30px;

@mixin outer {
  //background: blue;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  position: relative;
  width: 100%;
  margin: 0;
  @include padding($spacing null);
}

@mixin inner {
  //background: green;
  flex: 0 1 100%;
  @include margin(null auto);
  padding: $spacing $spacing / 2;

  @media #{$tablet} {
    flex-basis: $medium-screen;
    max-width: $medium-screen;
    padding: $spacing * 1.5 $spacing * 2;
  }

  @media #{$desktop} {
    flex-basis: $large-screen;
    max-width: $large-screen;
    padding: $spacing * 2 $spacing * 3;
  }
}

@mixin contents {
  //background: red;
  @include margin(null auto);
  @include padding(1px null);
  position: relative;

  //smaller widget for readability
  @media #{$desktop} {
    //width: 83.3333%;
  }
}
