html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  //height: 100%; TODO: Why is this here?
}

body {
  background-color: $base-background-color;
}

.site {
  overflow: hidden;
}

body:not(.page-builder) {
  #primary {
    @extend .col,
            .col-8;
    @include margin(null auto);
  }
}

.layout-sidebar:not(.page-builder) {
  #secondary {
    @extend .col,
            .col-4;
    @include margin(null auto);
    @media #{$desktop} {
      padding-left: 80px;
    }
  }
}

